.global-error-message__container {
  position: absolute;
  height: 200%;
  animation-name: show-in, show-out;
  animation-duration: 6s, 3s;
  animation-delay: 0s, 6s;
  animation-timing-function: linear; 
  animation-fill-mode: forwards;
}


@keyframes show-in {
  0% {
    visibility: hidden;
    top: 300px;
    left: 1040px;
  }
  50% {
    visibility: visible;
    top: 480px;
  }
 
  100% {
    visibility: visible;
    top: 480px;
    left: 1040px;
  }
}

@keyframes show-out {
  0% {
    visibility: visible;
    top: 480px;
    left: 1040px;
  }
 
  100% {
    visibility: hidden;
    top: 300px;
    left: 1040px;
  }
}


.global-error-message {
  position: sticky;
  top: 300px;
  color:darkorange;
  width: 100%;
  height: 30px;
  border-radius: 5px;
  padding: 30px 0px 35px 25px; 
  background-color:rgba(209, 243, 108, 0.594);
}

.global-error-message__lable {
  margin-bottom: 5px;
  color:rgb(79, 79, 81);
}